import React,{Suspense} from "react";
import { Canvas } from '@react-three/fiber';
import {OrbitControls} from '@react-three/drei'

import Pillow from './Pillow';
const Scene = ({x,y,z,scaleZ})=> {
  return(
      <Canvas shadows camera={{ position: [0, 0, 4], fov: 23 }} style={{display:'flex',justifyContent:'center',alignItems:'center',zIndex:'100',width:'100%',boxSizing:'border-box'}} >
        <Suspense fallback={null}>
        <ambientLight intensity={0.5} />
        <spotLight intensity={0.9} angle={0.1} penumbra={1} position={[20, 5, 10]} castShadow />
          <Pillow x={x} y={y} z={z} scaleZ={scaleZ}/>
        <OrbitControls enableZoom={false} enablePan={false} enableRotate={false} />
        </Suspense>
      </Canvas>
      );
}

export default Scene;